/* stylelint-disable */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.header-wrp {
  padding-top: 36px;
  padding-bottom: 36px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  color: #192252;
  font-size: 32px;
  line-height: 39px;
}
.header__action {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  color: #fff;
  background-color: #0E1734;
  padding: 14px 16px;
  border-radius: 3px;
  font-size: 16px;
  line-height: 19px;
}

.header-menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
.header-menu__item {
  margin-right: 32px;
}
.header-menu__item:last-child {
  margin-right: 0px;
}
.header-menu__link {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  color: #192252;
  font-size: 16px;
  line-height: 19px;
  cursor: pointer;
}

.screen1 {
  padding-bottom: 80px;
}
.screen1__left {
  max-width: 587px;
}
.screen1__title {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  color: #192252;
  font-size: 48px;
  line-height: 72px;
  margin-bottom: 24px;
}
.screen1__descr {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  color: #848FAC;
  margin-bottom: 32px;
  margin-right: 24px;
}
.screen1__link {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  color: #fff;
  line-height: 19px;
  padding: 16px 32px;
  background-color: #0E1734;
  border-radius: 3px;
}
.screen1__right {
  padding-top: 80px;
  display: flex;
}
.screen1__image {
  width: 180px;
  height: 588px;
  margin-right: 24px;
}
.screen1__image_down {
  margin-top: 81px;
}

.screen-wrp {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

